<template>
    <div class="couponCenter" can-swipe-back="false">
        <!-- <van-nav-bar class="task-nav-bar" title="领券中心" :border="false" left-arrow @click-left="goBack" /> -->


        <div class="shopBar header-view" :style="blackHeaderStyle" :class="isNavBar?'whiteNavBar':''">
            <div slot="left" class="leftBar">
                <div class="backIcon" @click="goBack()">
                    <img :src="isNavBar?icon.backBlack:icon.backWhite" alt="">
                </div>
                <div class="searhInputBox" :class="isNavBar?'searhInputBoxAct':''">领券中心</div>
            </div>

        </div>
        <div class="couponCenterContent" ref="contentScroll" @scroll="onScroll()">

            <div ref="imgHeight">
                <img style="width: 100%" src="@/assets/images/mall/bg_coupon_center.png" alt="">
            </div>
            <div class="couponList" v-if="getPickupList.length!=0">
                <div class="couponItem" v-for="(getPickupItem,i) in getPickupList" :key="'getPickupItem'+i">
                    <!-- <div class="couponInfo"  :class="getPickupItem.isPicked==1?'carCouponInfo':''"> -->
                    <div class="couponInfo"
                        :class="(getPickupItem.isFinished==1&&getPickupItem.isPicked!=1)?'grayCouponInfo':(getPickupItem.isPicked==1?'carCouponInfo':'')">
                        <div class="couponBox">
                            <!-- <div class="couponFirst">
                                <div class="couponPriceBox">
                                    <div class="couponPriceNum">
                                        <span>￥</span>
                                        <span>{{getPickupItem.couponTotal}}</span>
                                    </div>
                                    <div class="couponPriceTxt">
                                        <div>{{getPickupItem.shopName}}</div>
                                        <div>{{getPickupItem.name}}</div>
                                    </div>
                                </div>
                                <div class="couponRight" v-if="getPickupItem.isPicked==0&&getPickupItem.isFinished==0">
                                    <div class="receiveBtn" @click="receiveBtn(getPickupItem.id)">立即领取</div>
                                </div>
                                <div class="couponRight" v-if="getPickupItem.isPicked==1">
                                    <div class="receiveBtn" @click="goshop(getPickupItem.shopCode)">立即使用</div>
                                </div>
                            </div>
                            <div class="couponSec">
                                <div class="timeTxt" v-if="getPickupItem.effType == '1'">
                                    {{getPickupItem.effBeginDay}}-{{getPickupItem.effEndDay}}</div>
                                <div class="timeTxt" v-if="getPickupItem.effType == '2'">领取后{{getPickupItem.effDays}}天有效
                                </div>
                                <div class="rangeBox" @click="changeSeeRange(getPickupItem)">
                                    <div>适用范围</div>
                                    <img src="@/assets/images/mall/mall_arrow_down.png" alt="" v-if="!getPickupItem.seeRange">
                                    <img src="@/assets/images/mall/mall_arrow_up.png" alt="" v-if="getPickupItem.seeRange">
                                </div>
                            </div> -->
                            <div class="couponFirst">
                                <div class="couponPriceBox">
                                    <div class="couponPriceNum">
                                        <span>￥</span>
                                        <span>{{getPickupItem.couponTotal}}</span>
                                    </div>
                                    <div class="rangeBox" @click="changeSeeRange(getPickupItem)">
                                        <div>适用范围</div>
                                        <img src="@/assets/images/mall/mall_arrow_down.png" alt=""
                                            v-if="!getPickupItem.seeRange">
                                        <img src="@/assets/images/mall/mall_arrow_up.png" alt=""
                                            v-if="getPickupItem.seeRange">
                                    </div>
                                </div>
                                <div class="couponRight">
                                    <div class="couponPriceTxt">
                                        <div>{{getPickupItem.shopName}}</div>
                                        <div>{{getPickupItem.name}}</div>
                                    </div>
                                    <div class="couponBottom">
                                        <div class="timeTxt" v-if="getPickupItem.effType == '1'||getPickupItem.effBeginDay">
                                            {{getPickupItem.effBeginDay}}-{{getPickupItem.effEndDay}}
                                        </div>
                                        <div class="timeTxt" v-if="getPickupItem.effType == '2'&&getPickupItem.effDays == 1&&!getPickupItem.effBeginDay">
                                            领取后当天有效
                                        </div>
                                        <div class="timeTxt" v-if="getPickupItem.effType == '2'&&getPickupItem.effDays != 1&&!getPickupItem.effBeginDay">
                                            领取后{{getPickupItem.effDays}}天内有效
                                        </div>
                                        <div class="couponBtn"
                                            v-if="getPickupItem.isPicked==0&&getPickupItem.isFinished==0">
                                            <div class="receiveBtn" @click="receiveBtn(getPickupItem)">领取</div>
                                        </div>
                                        <div class="couponBtn" v-if="getPickupItem.isPicked==1">
                                            <div class="receiveBtn" @click="goshop(getPickupItem.shopCode)">去使用</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                    <div class="couponContent" v-show="getPickupItem.seeRange">
                        全品类通用
                    </div>
                </div>
            </div>
            <div class="noCouponList" v-if="getPickupList.length==0">
                <img src="@/assets/images/default/no_coupon_img.png" alt="">
                <div>暂无优惠券</div>
            </div>
        </div>

    </div>
</template>



<script>

    import { mapActions, mapState } from "vuex";
    import { Toast, Dialog } from "vant";
    import { queryPickupListService, receiveBtnService } from '../../services/goodsService.js';



    export default {
        name: 'couponCenter',
        components: { },
        data() {
            return {
                isNavBar: false,
                getPickupList: [],
                isTopBool: false,
                icon: {
                    backWhite: require("@/assets/images/common/nav_return_white.png"), //透明状态下 返回
                    backBlack: require("@/assets/images/common/nav_return_black.png"), //透明状态下 返回
                },

            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
            this.queryPickupList(); //获取优惠券列表
            this.isTopBool = true;
        },
        activated() {
            // this.resetData();

        },
        computed: mapState({
            // userInfo: this.$userInfo,
            blackHeaderStyle: function () {
                let hv = this.$stbHeight + 44 + 'px !important'; //68px
                let pv = this.$stbHeight + 'px'; //24px
                return { height: hv, 'padding-top': pv };
            },
        }),
        methods: {

            onScroll() {
                let scrollTop = this.$refs.contentScroll.scrollTop;
                let imgHeight = this.$refs.imgHeight.clientHeight;
                console.log(scrollTop)
                if (scrollTop > imgHeight - this.$stbHeight - 44) {
                    this.isNavBar = true;
                } else {
                    this.isNavBar = false;
                }
            },


            /**
             * 获取店铺优惠券列表
             * @param
             * shopCode  Number      店铺CODE
             * ownerCode Number      用户CODE
             * areaCode  Number      小区CODE
             **/
            queryPickupList() {
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    areaCode: this.$userInfo.AREA_CODE
                }
                queryPickupListService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.getPickupList = res.data.date;
                    }
                }).catch(error => {
                });
            },
            /**
             * 领取优惠券
             * @param
             * id        Number      优惠券CODE
             * ownerCode Number      用户CODE
             * areaCode  Number      小区CODE
             **/
            receiveBtn(getPickupItem) {
                var _this = this;
                if (!this.$userInfo.ROOM_CODE) {
                    Dialog.confirm({
                        title: "您未绑定房间，是否前往绑定？",
                        confirmButtonText: "前往"
                    }).then(() => {
                        //跳转到绑定房产页面
                        this.$router.push("selectArea");
                    }).catch(() => {
                    });
                    return
                }
                let paramData = {
                    id: getPickupItem.id,
                    ownerCode: this.$userInfo.OWNER_CODE,
                    areaCode: this.$userInfo.AREA_CODE,
                    roomCode: this.$userInfo.ROOM_CODE
                }
                receiveBtnService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        Toast("领取优惠券成功");
                        // getPickupItem.isPicked = 1;
                        _this.queryPickupListByItem(getPickupItem);
                    } else {
                        Toast(res.msg);
                    }
                }).catch(error => {
                    Toast(error)
                });
                //
            },

                        /**
             * 获取店铺优惠券列表
             * @param
             * shopCode  Number      店铺CODE
             * ownerCode Number      用户CODE
             * areaCode  Number      小区CODE
             **/
            queryPickupListByItem(getPickupItem) {
                // debugger;
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    areaCode: this.$userInfo.AREA_CODE
                }
                queryPickupListService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        debugger;
                        // this.getPickupList = res.data.date;
                        console.log(getPickupItem);
                        console.log("1111111111")
                        console.log(this.getPickupList);
                        
                        for(var i=0;i<this.getPickupList.length;i++){
                            if(this.getPickupList[i].id == getPickupItem.id)
                            {
                                for(var j=0;j<res.data.date.length;j++){
                                    var item1 = res.data.date[j];
                                    if(item1.id == getPickupItem.id)
                                    {
                                        this.getPickupList[i].effBeginDay = item1.effBeginDay;
                                        this.getPickupList[i].effEndDay = item1.effEndDay;
                                        this.getPickupList[i].isPicked = item1.isPicked;
                                        this.getPickupList[i].effType = item1.effType;
                                        this.getPickupList[i].effDays = item1.effDays;
                                    }
                                }
                            }
                        }
                        debugger
                        console.log("222222222")
                        console.log(this.getPickupList);
                        this.$forceUpdate();
                    }

                    
                }).catch(error => {
                });
            },
            //跳转优惠券对应店铺
            goshop(shopCode) {
                this.$router.push({ name: "shopPage", params: { shopCode: shopCode } });
            },


            handleScroll() {
                var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                var offsetTop = this.$refs.sideBar.offsetTop;
                if (scrollTop >= 130) {
                    this.isTopBool = false;
                } else {
                    this.isTopBool = true;
                }

            },
            changeSeeRange: function (getPickupItem) {
                getPickupItem.seeRange = !getPickupItem.seeRange;
                this.$forceUpdate();
                // $ionicScrollDelegate.resize(); //重新计算滑块大小
            },


            goBack: function () {
                this.$router.back();
            },
        }
    }
</script>

